
.popup{
  position: fixed;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s linear;
  z-index: 10;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  right: 0;
  bottom: 0;
}
