.form__title{
  width: 358px;
  min-height: 23px;
  margin: 34px 0 24px 0;
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}

@media (min-width: 320px) and (max-width: 500px) {
  .form__title {
    margin: 25px 0 45px 0;
    width: 238px;
    height: 22px;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
  }
}