.page {
  background-color: #000000;
  font-family: 'Inter', sans-serif;
  color: #E5E5E5;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
